<template>
  <div id="addDepartment">
    <v-row justify="center">
      <v-col cols="12">
        <v-btn
          color="primary"
          dark
          class="mb-2"
          @click.stop="dialog = true"
        >新增</v-btn>
        <v-dialog v-model="dialog" persistent max-width="700px">
          <!-- <base-material-card color="success" inline title="续费" class="px-4 py-2" style> -->
          <v-card>
            <v-card-title>
              <span class="headline">新增续费</span>
            </v-card-title>
            <v-card-text>
              <v-form class="pt-1" v-model="valid" :lazy-validation="lazy">
                <v-row align="center">
                  <v-text-field
                    :rules="numberRules"
                    required
                    label="续约天数(*必填)"
                    v-model="renewalDays"
                    outlined
                    class="ml-2"
                    cols="12"
                    sm="6"
                  />
                </v-row>

                <v-row align="center">
                  <v-text-field
                    :rules="nameRules"
                    required
                    label="版本(*必填)"
                    v-model="version"
                    outlined
                    class="ml-2"
                    cols="12"
                    sm="6"
                  />
                </v-row>

                <v-row align="center">
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    :return-value.sync="date"
                    min-width="200px"
                    offset-y
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                        v-model="date"
                        :rules="nameRules"
                        color="red"
                        label="开始续约时间"
                        readonly
                        v-on="on"
                        outlined
                        class="ml-2"
                        cols="12"
                        sm="6"
                      />
                    </template>

                    <v-date-picker v-model="date" color="secondary" scrollable locale="zh-cn">
                      <v-spacer />
                      <v-btn color="secondary" large @click="menu = false">Cancel</v-btn>

                      <v-btn color="secondary" large @click="$refs.menu.save(date)">OK</v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-row>
              </v-form>
              <v-row align="center">
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="doCancel()">关闭</v-btn>

              <v-btn color="blue darken-1" :disabled="!valid" text @click="doSave(cUniqueCode)">保存</v-btn>
            </v-row> 
            </v-card-text>
            
          </v-card>

          <!-- </base-material-card> -->
        </v-dialog>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "AddRenewalInfo",

  data: () => ({
    dialog: false,
    date: "",
    menu: false,
    companyUniqueCode: "",
    renewalDays: "",
    renewalTime: "",
    version: "",

    lazy: false,
    valid: true,
    //表单验证
    nameRules: [v => !!v || "该选项为必填字段"],
    numberRules:[(v => !!v || "该选项为必填字段"),(v => /^[0-9]*$/.test(v) || "该选项只能填数字" )],
  }),

  props: {
    cUniqueCode: {
      type: String
    }
  },

  methods: {
    doSave(cUniqueCode) {
      if (cUniqueCode) {
        this.$emit(
          "addRenewal",
           cUniqueCode,
          this.renewalDays,
          this.date,
          this.version
        );
      }
       this.dialog = false;
    },
    doCancel() {
      this.dialog = false;
    }
  }
};
</script>
